.value {
   color: white;
   font-size: 50px;
   margin: 0;
   padding: 20px;
   background: rgb(177, 24, 177);
   & input,
   textarea {
      color: black;
      width: 191.7px;
      font-size: 30px;
   }
   & textarea {
      width: 800px;
      height: 100px;
   }
   & button {
      width: 50px;
      height: 50px;
      font-size: 40px;
      &:last-child {
         width: 150px;
      }
   }
   .mainButton {
      width: 200px;
      font-size: 20px;
   }
   div {
      font-size: 20px;
   }
   & .calc {
      margin-top: 50px;
   }
}

@media screen and (max-width: 900px) {
   .value {
      & textarea {
         width: 600px;
         height: 100px;
      }
   }
}
@media screen and (max-width: 700px) {
   .value {
      & textarea {
         width: 400px;
         height: 100px;
      }
   }
}
@media screen and (max-width: 450px) {
   .value {
      & textarea {
         width: 90%;
         height: 100px;
      }
   }
}
